function App() {
  const [todos, settodos] = useState([]);
  const [value, setvalue] = useState("");
  const [ind, setind] = useState(null);
  const [editValue, seteditvalue] = useState("");
  function add() {
    if (value == "") {
      Swal.fire("Empty Not Allow");
    } else {
      settodos([...todos, value]);
      setvalue("");
    }
  }

  function editBtn(i) {
    Swal.fire({
      title: "Do you want to Edit this  Todo?",
      showCancelButton: true,
      confirmButtonText: "Yes",
    }).then((result) => {
      if (result.isConfirmed) {
        setind(i);
        let getEditValue = todos[i];
        seteditvalue(getEditValue);
      }
    });
  }
  function update(i) {
    todos[i] = editValue;
    setind(null);
    seteditvalue("");
    Swal.fire("Updated!", "You clicked the button!", "success");
  }

  function dellAll() {
    settodos([]);
    Swal.fire("Delete All!", "You clicked the button!", "success");
  }
  function dell(i) {
    let dellItem = todos.filter((item, a) => {
      if (a != i) {
        console.log("------>>>" + item);
        return item;
      }
    });
    console.log(dellItem);
    settodos(dellItem);
  }
  return (
    <div className="body">
      <div className="heading">Todo App</div>
      <div className="main">
        <input
          className="form-control"
          placeholder="TODO"
          value={value}
          onChange={(e) => setvalue(e.target.value)}
        />{" "}
        <div className="btns">
          <button className="add " onClick={() => add()}>
            <i class="fa-solid fa-plus"></i>{" "}
          </button>
          <button className="d_all" onClick={() => dellAll()}>
            <i class="fa-solid fa-trash"></i>
          </button>
        </div>
      </div>
      <div>
        {
          todos.map((v, i) => {
            return (
              <>
                {ind === i ? (
                  <li key={i} className="  li ">
                    {" "}
                    <div className=" li_text">
                      {" "}
                      <input
                        className="form-control"
                        value={editValue}
                        onChange={(e) => seteditvalue(e.target.value)}
                      />
                    </div>
                    <button
                      className=" upBtn"
                      onClick={() => {
                        update(i);
                      }}
                    >
                      {" "}
                      <i class="fa-solid fa-pen-nib"></i>
                    </button>
                  </li>
                ) : (
                  <>
                    {" "}
                    <li className=" li" key={i}>
                      {" "}
                      <div className="  li_text">{v}</div>
                      <div className="btns">
                        <button
                          className="  "
                          onClick={() => {
                            editBtn(i);
                          }}
                        >
                          {" "}
                          <i class="fa-solid fa-pen-to-square"></i>
                        </button>
                        <button onClick={() => dell(i)}>
                          {" "}
                          <i class="fa-solid fa-trash"></i>
                        </button>
                      </div>
                    </li>
                  </>
                )}
              </>
            );
          })
          // <li> {todos} </li>
        }
      </div>
    </div>
  );
}

export default App;
